import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
    {
        path: '/activity/draw',
        component: () => import('../views/draw-activity/DrawActivity')
    },
    {
        path: '/code',
        component: () => import('../views/code-view/CodeView')
    },
    {
        path: '/c',
        component: () => import('../views/code-view/CodeView')
    },
    {
        path: '/code/simple',
        component: () => import('../views/code-view/SimpleCodeView')
    },
    {
        path: '/code/trace',
        component: () => import('../views/code-view/CodeTrace')
    },
    {
        path: '/code/attr-category-trace',
        component: () => import('../views/code-view/AttrCategoryTrace')
    },
    {
        path: '/error',
        component: () => import('../views/error/Error')
    },
    {
        path: '/check-code-history',
        component: () => import('../views/check-code-history/CheckCodeHistory')
    },
    {
        path: '/contact-us',
        component: () => import('../views/contact-us/ContactUs')
    },
    {
        path: '/page/:pageId',
        component: () => import('../views/file-display/FileDisplay')
    },
    {
        path: '/product-trace-rf/trace',
        component: () => import('../views/product-trace-rf/ProductTrace')
    },
    {
        path: '/trace/trace-analysis',
        component: () => import('../views/trace/TraceAnalysis.vue')
    }
]

const router = new VueRouter({
    mode: 'hash',
    base: process.env.BASE_URL + 'weixin',
    routes,
    scrollBehavior() {
        return { x: 0, y: 0 }
    }
})

export default router
